<template>
  <div id="login_box">
    <div class="login_logo">停车场注册页面</div>
    <div class="login_con">
		<div class="login_form"  >
			<el-form :model='parkingForm' :line='true' :rules='rules' 
			v-loading.fullscreen.lock="loading"   element-loading-text="拼命加载中..." >
			<!-- 第0行 -->
			<el-row >
				<el-col :span='8'>
					<el-form-item label='车场ID' prpo='parkingNo' >
						<el-input v-model='parkingForm.parkingNo'  style='width:60%'></el-input>
					</el-form-item>
				</el-col>
				<el-col :span="8">
						<el-form-item label='停车场详细地址' prpo='parkingAddress'>
								<el-input v-model='parkingForm.parkingAddress'  style='width:60%'></el-input>
							</el-form-item>
				</el-col>
			</el-row>


				<!-- 第一行 -->
				<el-row >
					<el-col :span='8' >
						<el-form-item label="车场名称" prop='name'>
						<el-input v-model="parkingForm.name" placeholder="请输入车场名称" style="width:60%;"></el-input>
						</el-form-item>
					</el-col>
					<el-col :span='8' >
							<el-form-item label="经度坐标" prop='longitude'>
							<el-input v-model="parkingForm.longitude" placeholder="请输入经度" style="width:60%;"></el-input>
							</el-form-item>
					</el-col>
					<el-col :span='8' >
							<el-form-item label="纬度坐标" prop='latitude'>
							<el-input v-model="parkingForm.latitude" placeholder="请输入纬度" style="width:60%;"></el-input>
							</el-form-item>
					</el-col>
				</el-row>
				<!-- 第二行 -->
				<el-row  >
					<el-col :span='8' >
						<el-form-item label="总车位数" prop='totalSpace'>
						<el-input v-model="parkingForm.totalSpace" placeholder="请输入总车位数" style="width:60%;"></el-input>
						</el-form-item>
					</el-col>
					<el-col :span='8' >
						<el-form-item label="车场所有人" prop='Owner'>
							<el-input v-model="parkingForm.Owner" placeholder="请输入车场所有人" style="width:60%;"></el-input>
						</el-form-item>
					</el-col>
					<el-col :span='8' >
						<el-form-item label="是否支持网上预约" prop='parkingOnlineValue'>
								<el-select v-model='parkingForm.parkingOnlineValue' placeholder='请选择' style="width:40%" clearable>
									<el-option
										v-for='item in parkingOnlineOptions'
										:key="item.id"
										:label='item.label'
										:value='item.value'>
									</el-option>
								</el-select>
							</el-form-item>
					</el-col>
				</el-row>
				<!-- 第三行 -->
				<el-row >
						<el-col :span='8' >
							<el-form-item label="所属城市" prop='cityName'>
								<el-input v-model="parkingForm.cityName" placeholder="请输入所属城市" style="width:60%;"></el-input>
							</el-form-item>
						</el-col>
						<el-col :span='8' >
							<el-form-item label="车场类型" prop='parkingTypeValue'>
								<el-select v-model='parkingForm.parkingTypeValue' placeholder='请选择' clearable>
									<el-option
										v-for='item in parkingTypeOptions'
										:key="item.id"
										:label='item.label'
										:value='item.value'>
									</el-option>
								</el-select>
							</el-form-item>
						</el-col>
						<el-col :span='8' >
							<el-form-item label="车场种类" prop='parkingKindValue'>
								<el-select v-model='parkingForm.parkingKindValue' placeholder='请选择' clearable>
									<el-option
										v-for='item in parkingKindOptions'
										:key="item.id"
										:label='item.label'
										:value='item.value'>
									</el-option>
								</el-select>
							</el-form-item>
						</el-col>
				</el-row>
				<!-- 第四行 -->
				<el-row >
					<el-col :span='8' >
						<el-form-item label="出入口数量" prop='enterExitCount'>
							<el-input v-model="parkingForm.enterExitCount" placeholder="请输入入口数" style="width:60%;" @blur="handleBlurEnter(parkingForm.enterExitCount)"></el-input>
							<!-- <el-button type='button' class='el-button el-button--primary' @click='getEnterMsg()'>修改</el-button> -->
						</el-form-item>
					</el-col>

				</el-row>
				<div style='float:right;margin:20px 15% 0 0;'>
						<el-button type='button' class='el-button el-button--warning' style="width: 80px;margin-right:20px;" @click='cancelExit()'>取消</el-button>
					<el-button type='button' class='el-button el-button--primary' style="width:80px" @click='handleRegister()'>注册</el-button>
				</div>
				<div style='clear:both'></div>

			</el-form>
    	</div>
	</div>
	<!-- 入口方向模态框 -->
	<el-dialog title='进出口类型' :visible.sync='dialogVisibleEnter' width='1000px'>
		<el-form :model='enterExitForm'>
			<div  v-for='(item,index) in enterExitForm.ztnr' :key='index' style='margin-bottom:10px;'>
				<div class='dynamicBox'>
					<el-row >
							<el-col :span='6'>
									<el-form-item label='进出口ID' class='laeblStyle2'>
										<el-input v-model='item.enterExitNo' class='handle-input'></el-input>
									</el-form-item>
								</el-col>
								<el-col :span='1'>
										&nbsp;
									</el-col>
						<el-col :span='7' >
							<el-form-item label='进出口类型' class='laeblStyle' >
								<el-select v-model='item.jcklx' class='handle-input' >
									<el-option
									v-for='item in typeNoOptions'
									:key='item.id'
									:label='item.label'
									:value='item.value'>
									</el-option>
								</el-select>
							</el-form-item>
					</el-col>
					<el-col :span='8'>
							<el-form-item label='名称'>
								<el-input v-model='item.enterExitName' style='width:80%'></el-input>
							</el-form-item>
						</el-col>
					<!-- 数量、名称、编号、 -->
				</el-row>
				<el-row>
						<el-col :span='6'>
								<el-form-item label='经度' class='laeblStyle2'>
								<el-input v-model='item.enterExitLongitude' class='handle-input'></el-input>
							</el-form-item>
					</el-col>
					<el-col :span='1'>
							&nbsp;
						</el-col>
					<el-col :span='6'>
						<el-form-item label='纬度' class='laeblStyle'>
							<el-input v-model='item.enterExitLatitude' class='handle-input'></el-input>
						</el-form-item>
					</el-col>
				</el-row>
				</div>
			</div>
					<div style='float:right;margin:20px 15% 0 0;'>
							<el-button type='button' class='el-button el-button--warning' style="width: 80px;margin-right:20px;" @click='cancelEnter()'>取消</el-button>
						<el-button type='button' class='el-button el-button--primary' style="width:80px" @click='saveEnter()'>保存</el-button>
					</div>
					<div style='clear:both'></div>
		</el-form>
	</el-dialog>

  </div>
</template>
   
<script>
export default {
	data () {
		var validatorRule=function(rule,value,callback){
			if(!value){
				callback(new Error(rule.message));
			}
		}
		return {
			rules:{
				parkingNo:[{required: true, message: '车场ID不能为空', trigger: 'blur', validator: validatorRule}],
				parkingAddress:[{required: true, message: '停车详细地址不能为空', trigger: 'blur', validator: validatorRule}],
				name:[{required: true, message: '车场名称不能为空', trigger: 'blur', validator: validatorRule}],
				longitude:[{required: true, message: '经度不能为空', trigger: 'blur', validator: validatorRule}],
				latitude:[{required: true, message: '维度不能为空', trigger: 'blur', validator: validatorRule}],
				totalSpace:[{required: true, message: '总车位数不能为空', trigger: 'blur', validator: validatorRule}],
				enterCount:[{required: true, message: '入口数量不能为空', trigger: 'blur', validator: validatorRule}],
				exitCount:[{required: true, message: '出口数量不能为空', trigger: 'blur', validator: validatorRule}],
				cityName:[{required: true, message: '所属城市不能为空', trigger: 'blur', validator: validatorRule}],
				parkingTypeValue:[{required: true, message: '车场类型不能为空', trigger: 'blur', validator: validatorRule}],
				parkingKindValue:[{required: true, message: '车场种类不能为空', trigger: 'blur', validator: validatorRule}],
				Owner:[{required: true, message: '车场所有人不能为空', trigger: 'blur', validator: validatorRule}],
				enterDirection:[{required: true, message: '入口方向不能为空', trigger: 'blur', validator:validatorRule}],
				exitDirection:[{required: true, message: '出口方向不能为空', trigger: 'blur', validator: validatorRule}],
				parkingOnlineValue:[{required: true, message: '请选择预约方式', trigger: 'change', validator: validatorRule}],
			},
			parkingForm:{},	//停车场信息表单
			parkingTypeValue:"",
			parkingTypeOptions:[
				{id:"0",value:"0",label:"非运营"},
				{id:"1",value:"1",label:"运营"},
			],
			parkingKindValue:"",
			parkingKindOptions:[
				{id:"1",value:"1",label:"封闭式"},
				{id:"2",value:"2",label:"开放式"},
			],
			parkingOnlineValue:"",
			parkingOnlineOptions:[
				{id:"1",value:'1',label:"是"},
				{id:"2",value:'0',label:"否"},
			],
			enterCount:[],//入口数量
			dialogVisibleEnter:false,
			enterExitForm:{
				ztnr:[]
			},//出入口表单信息
			enterCountLabel:[],
			cacheEnterMsg:[],	//缓存出口信息
			/** 出口数量、模态框、出口方向表单、*/
			exitCount:[],
			dialogVisibleExit:false,
			exitForm:{},
			loading:false,
			typeNoOptions:[],
			enterExitLength:[]
		}
	},
	mounted:function() {
		// this.getTypeOptions();
	},
	created:function(){
		this.getTypeOptions();
        },
	methods:{
		handleRegister(){
			var self =this;
			var dataArr=[];
			var enterExitInfos=[];
			var rk=self.enterCount;
			var ck=self.exitCount;
			var max;
			var enterDirection=[];
			var exitDirection=[];
			//判断出、入口是否有值;
			var max=self.exitCount.length+self.enterCount.length;
			for(var i=0;i<self.enterExitForm.ztnr.length;i++){
				enterExitInfos[i]={
					"enterExitType":self.enterExitForm.ztnr[i].jcklx,
					"typeCount":self.enterExitForm.ztnr[i].typeCount,
					"enterExitNo":self.enterExitForm.ztnr[i].enterExitNo,
					"enterExitName":self.enterExitForm.ztnr[i].enterExitName,
					"enterExitLongitude":self.enterExitForm.ztnr[i].enterExitLongitude,
					"enterExitLatitude":self.enterExitForm.ztnr[i].enterExitLatitude,
					"enterExitKind":1,
				}
			}
			console.log("出入口方向")
			console.log(enterExitInfos);
			var params={
				"userId":localStorage.getItem("userId"),
				"parkingNo":self.parkingForm.parkingNo,
				"parkingName":self.parkingForm.name,
				"longitude":self.parkingForm.longitude,
				"latitude":self.parkingForm.latitude,
				"totalSpace":self.parkingForm.totalSpace,
				"cityName":self.parkingForm.cityName,
				"parkingAddress":self.parkingForm.parkingAddress,
				"parkingKind":self.parkingForm.parkingKindValue,
				"parkingType":self.parkingForm.parkingTypeValue,
				"parkingOwner":self.parkingForm.Owner,
				"onlineReservation":self.parkingForm.parkingOnlineValue,
				"enterExitCount":self.parkingForm.enterExitCount,
				'enterExitInfos':enterExitInfos,
			}

			self.loading=true;
			var url=this.baseUrl +"parkingInfo/add";
			this.$axios.post(url,params).then(function(res){
				if(res.data.status=='0'){
					self.loading=false;
					self.$message({
						message:"保存成功!",
						type:"success"
					})
					self.$router.push("/testMsg");
				}else{
					self.loading=false;
					self.$message({
						message:res.data.message,
						type:"error"
					})
				}
			}).catch(function(err){
				console.log(err)
			})
		},

		handleBlurEnter(val){
			var self=this;
			console.log("失去焦点 "+val)
			self.enterExitForm.ztnr=[];
			for(var i=0;i<val;i++){
				self.enterExitForm.ztnr.push({
					"jcklx":"",
					"typeCount":"",
					"enterExitNo":"",
					"enterExitLongitude":"",
					"enterExitLatitude":"",
					"enterExitName":"",							
				})
			}
			this.dialogVisibleEnter=true;	//模态框
		},
		saveEnter(){
			var self=this;
			console.log(self.enterExitLength);
			self.$message({
				message:"保存成功!",
				type:"success"
			})
			this.dialogVisibleEnter=false;
		},
		saveExit(){
			var self=this;
			self.$message({
				message:"保存成功!",
				type:"success"
			})
			this.dialogVisibleExit=false;
		},
		cancelEnter(){
			this.dialogVisibleEnter=false;
		},
		cancelExit(){
			this.dialogVisibleExit=false;
		},
		getEnterMsg(){
			if(this.enterCount.length=='0'){
				this.$message({
					message:"出入口数量不能为空!",
					type:"warning"
				})
				return;
			}
			this.dialogVisibleEnter=true;
		},
		getExitMsg(){
			if(this.exitCount.length=='0'){
				this.$message({
					message:"出口数量不能为空!",
					type:"warning"
				})
				return;
			}
			this.dialogVisibleExit=true;
		},
		addMsg(){
			var self=this;
			self.enterExitLength.push({
				"数据":1
			})
		},
		//进出口类型
		getTypeOptions(){
			var self=this;
			var url=self.baseUrl+'parkingInfo/enterExitType'
			self.$axios.get(url).then(function(res){
				for(var i=0;i<res.data.data.length;i++){
					self.typeNoOptions[i]={
						id:res.data.data[i].id,
						value:res.data.data[i].typeNo,
						label:res.data.data[i].typeName
					}
				}
			}).catch(function(err){
				console.log(err);
			})
		},
		delMsg(){
			var self=this;
			self.enterExitLength.pop();
		}
	}
}
</script>

<style scoped>
.dynamicBox{
        border-bottom:dashed 1px #c8c7cc;
        margin-top:10px;
        margin-bottom:20px;
        height:130px;
      }

	.laeblStyle{
		width:240px;
  	text-align:right
	}
.laeblStyle2{
		width:220px;
  	text-align:right
	}
.handle-input{
  width:140px;
  display: inline-block;
}	
#login_box{
    width: 100%;
    height: 100%;
    /* background: #0c2a61; */
    background: #fff;
    overflow: hidden;
}
.login_con{
	max-width: 1100px;
	height: 100%;
    /* background: url("../../../static/img/index.png") no-repeat left top; */
    background-size: 100% auto;
    margin: 10px auto 0;
    position: relative;
}
.login_logo{
	max-width: 1100px;
	margin: 30px auto;
	font-size: 32px;
	color: black;
	/* background: url("../../../static/img/web12.png") no-repeat 0px -5px; */
	background-size: auto 100%;
	text-align: left;
	height: 70px;
	line-height: 42px;
	padding-left: 80px;
}
.login_form{
	width: 100%;
	height: 65%;
	padding: 30px 20px;
	/* background: rgba(15, 53, 124, 0.8); */
    position: absolute;
    border-radius: 4px;
    /* box-shadow: 5px 5px 5px #0c285c; */
}
.hydl{
	color: #fff;
	font-size: 24px;
	text-align: center;
}
._btn{
	position:absolute;
	margin-top:-66px;
	right:18px;
}
.btnJS{
	position:absolute;
	margin-top:-66px;
	right:108px;
}
</style>



